<template>
  <div class="container text-center">
    <div class="container-message">
      <ModalError
        :reason.sync="reasonError"
        :message.sync="messageError"
        @clear-error="clearError"
        v-if="!!reasonError"
      />
    </div>
    <div v-if="!joined" id="loginPage" class="text-center">
      <form @submit="join">
        <button
          type="button"
          v-if="microphone"
          @click="toggleMic"
          class="btn-on"
        >
          <img src="../assets/img/mic.png" alt="" />
        </button>

        <button
          type="button"
          v-if="!microphone"
          @click="toggleMic"
          class="btn-off"
        >
          <img src="../assets/img/mic_slash.png" alt="" />
        </button>

        <button type="button" v-if="camera" @click="toggleCam" class="btn-on">
          <font-awesome-icon icon="fa-solid fa-video" />
        </button>

        <button type="button" v-if="!camera" @click="toggleCam" class="btn-off">
          <font-awesome-icon icon="fa-solid fa-video-slash" />
        </button>

        <button id="loginBtn" type="submit">Join</button>
      </form>
    </div>
    <div
      id="callPage"
      class="call-page"
      :class="{ disconected: joined === false }"
    >
      <div id="remoteVideo" ref="remoteVideo" class="divRemoteVideos">
        <div class="streamcontainer">
          <video
            :class="{ cameraOff: camera === false }"
            id="localVideo"
            ref="localVideo"
            autoplay
            muted
            controls
            disablePictureInPicture
          ></video>
          <span class="username local">{{ usernameFrom }}</span>
        </div>
      </div>
      <div class="footer-mc">
        <h6 v-if="screen" class="text-center warning">
          Estas compartiendo tu pantalla!!!
        </h6>
        <div class="panel">
          <button @click="hangUp" class="btn-off">
            <img src="../assets/img/hangup.png" alt="cortar" />
          </button>
          <button
            v-if="screen"
            ref="screen"
            @click="shareScreen"
            class="btn-on"
          >
            <img src="../assets/img/screen.png" alt="compatir" />
          </button>
          <button
            v-if="!screen"
            ref="screen"
            @click="shareScreen"
            class="btn-off"
          >
            <img src="../assets/img/screen.png" alt="dejar de compartir" />
          </button>
          <button v-if="microphone" @click="toggleMic" class="btn-on">
            <img src="../assets/img/mic.png" alt="" />
          </button>
          <button v-if="!microphone" @click="toggleMic" class="btn-off">
            <img src="../assets/img/mic_slash.png" alt="" />
          </button>
          <button v-if="camera" @click="toggleCam" class="btn-on">
            <img src="../assets/img/cam.png" alt="" />
          </button>
          <button v-if="!camera" @click="toggleCam" class="btn-off">
            <img src="../assets/img/cam_slash.png" alt="" />
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import ModalError from "../components/ModalError.vue";
import { CallHelper } from "../helpers/CallHelper";
import { PeerConnection } from "../peer/PeerConnection";
import { ShareScreen } from "../peer/ShareScreen";

export default {
  components: {
    ModalError,
  },
  data() {
    return {
      connection: null,
      screen: null,
      localVideoPermission: null,
      usernameTo: "",
      usernameFrom: "",
      room: "",
      joined: false,
      reasonError: "",
      microphone: true,
      camera: true,
    };
  },
  mounted() {
    this.usernameFrom = this.$route.query.username;
    this.room = this.$route.query.room;

    CallHelper.video = this.camera;
    CallHelper.audio = this.microphone;
    CallHelper.showError = this.clearError;
  },
  methods: {
    clearError(e) {
      console.log(e);
      this.reasonError = e;
    },
    async join(e) {
      e.preventDefault();

      try {
        CallHelper.localVideoSource = this.$refs.localVideo;
        this.localVideoPermission = await CallHelper.loadLocalVideo();

        this.connection = new PeerConnection(
          this.usernameFrom,
          this.localVideoPermission
        );
        await this.connection.connect();

        if (!this.camara || !this.microphone)
          await this.connection.toggleStatusCamAndMic(
            this.microphone,
            this.camera
          );

        this.joined = true;
      } catch (error) {
        CallHelper.showError(error);
      }
    },
    async shareScreen() {
      try {
        if (this.screen) {
          this.screen.disconnectCall();
          this.screen.peer.destroy();
          this.screen = null;
          return;
        }

        this.screen = new ShareScreen(this.usernameFrom);
        await this.screen.connect(this.room);
      } catch (error) {
        console.log(error);
        this.reasonError = error.message;
      }
    },
    hangUp() {
      CallHelper.removeAllSources();
      CallHelper.leaveCall(this.connection);

      if (this.screen) {
        this.connection.disconnectCall();
        this.connection.peer.destroy();
      }

      this.joined = false;
    },
    async getNewPermissionVideo() {
      CallHelper.video = this.camera = !this.camera;
    },
    async getNewPermissionAudio() {
      CallHelper.audio = this.microphone = !this.microphone;
    },
    async toggleCam() {
      this.camera = !this.camera;
      CallHelper.video = this.camera;

      if (CallHelper.permissionCamaraOrMic) {
        this.localVideoPermission = await CallHelper.loadLocalVideo();
        this.connection.changeSourceVideo(this.localVideoPermission);
      }

      await this.connection.toggleStatusCamAndMic(this.microphone, this.camera);
    },
    async toggleMic() {
      this.microphone = !this.microphone;
      CallHelper.audio = this.microphone;

      if (CallHelper.permissionCamaraOrMic) {
        this.localVideoPermission = await CallHelper.loadLocalVideo();
        this.connection.changeSourceVideo(this.localVideoPermission);
      }

      await this.connection.toggleStatusCamAndMic(this.microphone, this.camera);
    },
  },
};
</script>

<style>
.container {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background: no-repeat center/100% url("../assets/img/fondowp.png");
  width: 100vw;
  height: 100vh;
}

.text-center {
  text-align: center;
}

form {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  align-items: center;
  height: 100vh;
}

form input {
  line-height: 40px;
  font-size: 20px;
  border-radius: 15px;
  padding: 0 20px;
  outline-color: rgb(201, 200, 221);
}

form button {
  line-height: 32px;
  font-size: 32px;
  color: aliceblue;
  padding: 0 20px;
  background-color: #3ea06c;
  margin-left: 10px;
  border-radius: 15px;
  border-color: #3ea06c;
}

form button img {
  margin: auto;
}

form button:hover {
  color: white;
  background-color: #09c561;
  border-color: #09c561;
  cursor: pointer;
}

form button:disabled {
  background-color: rgb(93, 93, 131);
  cursor: none;
}

.login {
  max-width: 600px;
}

.disconected {
  display: none;
}

.divRemoteVideos {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90vh;
  --videoWidth: 700px;
  --videoHeight: auto;
}

.streamcontainer {
  position: relative;
}

.streamcontainer video {
  background: black;
  border: 5px solid gray;
  width: var(--videoWidth);
  max-width: 100vw;
  height: var(--videoHeight);
  max-height: 90vh;
  margin: 2px;
}

.streamcontainer video::-webkit-media-controls-play-button,
video::-webkit-media-controls-timeline,
video::-webkit-media-controls-toggle-closed-captions-button,
video::-webkit-media-controls-current-time-display {
  display: none;
}

.streamcontainer .username {
  position: absolute;
  top: 7px;
  left: 7px;
  color: aliceblue;
  background-color: black;
  font-weight: bold;
  margin: 0;
  padding: 0 5px;
  border-radius: 10%;
}

.streamcontainer .local {
  color: chartreuse;
}

/* form {
  margin: 40vh;
}
 */
.container-message {
  position: relative;
}

.footer-mc {
  display: flex;
  flex-direction: column;
  height: 10vh;
  justify-content: end;
  align-items: center;
}

.warning {
  font-size: 20px;
  color: rgb(255, 107, 1);
  margin: 0;
}

.panel {
  text-align: center;
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
  width: fit-content;
}

.panel button {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border-color: transparent;
  margin: 0 5px;
  justify-content: center;
  align-items: center;
}

.panel button img {
  width: 32px;
  height: auto;
}

.btn-on {
  background-color: rgb(125, 123, 123);
  color: whitesmoke;
}

.btn-off {
  background-color: rgb(245, 76, 76);
  color: whitesmoke;
}

/* .btn-off img {
  width: 20px;
  height: auto;
} */
</style>
